import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class PlaceService {

  constructor(
    private _http: Http
  ) { }
  getPlaces(){
    return this._http.get('api/v1/places',{}).map(res=>res.json());
  }
}



