import { Component, OnInit,ElementRef, Renderer,ViewChild } from '@angular/core';
import { ValidationsService } from '../../../../services/validations/validations.service';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { User } from '../../../../models/user';
import { Place } from '../../../../models/place';
import { OauthService } from '../../../../services/oauth/oauth.service';
import { PlaceService } from '../../../../services/place/place.service';
import { Credential } from '../../../../models/credential';
import { GLOBAL } from '../../../../services/enviroment/global';

@Component({
  selector: 'app-registration-step-one',
  templateUrl: './registration-step-one.component.html',
  styleUrls: ['./registration-step-one.component.sass'],
  providers:[ValidationsService,OauthService,PlaceService]
})
export class RegistrationStepOneComponent implements OnInit {
  public validateData:any;
  public user:User;
  private credential:Credential;
  private key:string;
  public places:Place[];
  public ubicationAux:string;
  public passwordRepat:string;
  @ViewChild('name') name:ElementRef;
  @ViewChild('lastName') lastName:ElementRef;
  @ViewChild('password') password:ElementRef;
  @ViewChild('passwordRepeat') passwordRepeat:ElementRef;
  @ViewChild('ubication') ubication:ElementRef;
  constructor(
    private _validationsService:ValidationsService,
    private _oauthService:OauthService,
    private _route:ActivatedRoute,
    private _router:Router,
    private _placeService:PlaceService
  ) { 
    this.validateData={
      name:true,
      lastName:true,
      password:true,
      passwordRepeat:true,
      ubication:true
    }
    this.user = new User();
    this.user.last_name="";
    this.user.first_name="";
    this.user.password="";
    this.user.email="";
    this.passwordRepat="";
    this.credential=new Credential(GLOBAL.userAdmin,GLOBAL.passwodrAdmin);
  }
  ngOnInit() {
    if(this.checkEmail()){
      if(typeof(Storage)!="undefined"){
        if(localStorage.getItem("key")!=null){
          localStorage.removeItem("key");
        }
        //this.authentication();
      }else{
        alert("Update your navigator");
      }
    }
    this.getPlaces();
  }
  validateStepOne(){
    console.log(this.ubication.nativeElement.value);
    if(this.validatePersonalData()){
      this.storageOnSession();
    }
  }
  authentication(){
    this._oauthService.login(this.credential).subscribe(
      success=>{
         this.key=success.token;
         if(this._oauthService.setLocalStorageKey(this.key)){
           
         }
      },
      error=>{
        alert("Resquest error");
      }
    );
  }
  validatePersonalData(){
    this.validateData={
      name:true,
      lastName:true,
      password:true,
      passwordRepeat:true,
      ubication:true
    }
    var validate = true;
    if(!this._validationsService.validateSomeName(this.name.nativeElement.value)){
      this.validateData.name=false;
      validate=false;
    }
    if(!this._validationsService.validateSomeName(this.lastName.nativeElement.value)){
      this.validateData.lastName=false;
      validate=false;
    }
    if(!this._validationsService.validatePassword(this.password.nativeElement.value)){
      this.validateData.password=false;
      validate=false;
      var $toastContent = $('<span>La contraseña debe tener<br> mínimo6 caracteres <br></span>').add($('<button class="btn-flat toast-action">Undo</button>'));
      Materialize.toast($toastContent, 10000);
    }
    if(this.password.nativeElement.value!=this.passwordRepeat.nativeElement.value){
      this.validateData.passwordRepeat=false;
      validate=false;
    }
    if(this.ubication.nativeElement.value==''){
      this.validateData.ubication=false;
      validate=false;
    }
    return validate;
  }
  storageOnSession(){
    this.user.first_name=this.name.nativeElement.value;
    this.user.last_name=this.lastName.nativeElement.value;
    this.user.password=this.password.nativeElement.value;
    this.user.place=this.ubication.nativeElement.value;
    if(typeof(Storage)){
      sessionStorage.setItem('register',JSON.stringify(this.user));
      this.toStepTwo();
    }
  }
  toStepTwo(){
    this._router.navigate(['/registerusertwo']);
  }
  getPlaces(){
    debugger;
    this._placeService.getPlaces().subscribe(
      success=>{
        this.places=success;
        console.log(this.places);
      },
      error=>{
        alert("Request Error");
      }
    );
  }
  checkEmail(){
    let success=true;
    this._route.params.forEach((params:Params)=>{
      if(params['email']!= null && params['email']!= undefined){
        this.user.email=params['email'];
      }else{
        this._router.navigate(['/registeruserone']);
        success=false;
      }
    });
    return success;
  }

}



