import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Rx';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/catch';
import { ConnectionBackend, RequestOptions, Request, RequestOptionsArgs, Response, Http, Headers} from "@angular/http";
import { GLOBAL } from '../enviroment/global';
import { Router, Params, ActivatedRoute } from '@angular/router';

//import { Token } from '../../models/token';
@Injectable()
export class InterceptedHttpService extends Http  {
private router:Router;
constructor(backend: ConnectionBackend, defaultOptions: RequestOptions,_router:Router) {
        super(backend, defaultOptions);
        this.router=_router;
    }

    get(url: string, options?: RequestOptionsArgs): Observable<Response> {
        url = this.updateUrl(url);
        return super.get(url, this.getRequestOptionArgs(options));
    }

    post(url: string, body: string, options?: RequestOptionsArgs): Observable<Response> {
        url = this.updateUrl(url);
        return super.post(url, body, this.getRequestOptionArgs(options));
    }

    put(url: string, body: string, options?: RequestOptionsArgs): Observable<Response> {
        url = this.updateUrl(url);
        return super.put(url, body, this.getRequestOptionArgs(options));
    }

    patch(url: string, body: string, options?: RequestOptionsArgs): Observable<Response> {
        url = this.updateUrl(url);
        return super.patch(url, body, this.getRequestOptionArgs(options));
    }

    delete(url: string, options?: RequestOptionsArgs): Observable<Response> {
        url = this.updateUrl(url);
        return super.delete(url, this.getRequestOptionArgs(options));
    }
    
    private updateUrl(req: string) {
        return  GLOBAL.apiGeepy + req;
    }

    private getRequestOptionArgs(options?: RequestOptionsArgs) : RequestOptionsArgs {
        if (options == null) {
            options = new RequestOptions();
        }
        if (options.headers == null) {
            options.headers = new Headers();
        }
        options.headers.append('Content-Type', 'application/json');
        var key="";
        if(typeof(Storage) !== "undefined") {
            key=localStorage.getItem("key");
            if(key!=undefined && key!=null){
              options.headers.append('Authorization','Token '+key);
            }
         }
        return options;
    }
    request(url: string | Request, options?: RequestOptionsArgs): Observable<Response> {
        return super.request(url, options).catch(this.catchErrors());
    }

    private errorAuth(){

    }
    
      private catchErrors() {
        return (res: Response) => {
            
          if (res.status === 401 || res.status === 403) {
            //handle authorization errors
            //in this example I am navigating to logout route which brings the login screen
            this.router.navigate(['/logout']);
          }
          return Observable.throw(res);
        };
      }
    /*request(url: string | Request, options?: RequestOptionsArgs): Observable<Response> {
        return this.intercept(super.request(url, options));
    }*/
}


