import { Component, OnInit,ElementRef,ViewChild  } from '@angular/core';
import { ValidationsService } from '../../services/validations/validations.service';
import { Observable } from 'rxjs/Observable';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { UserService } from '../../services/user/user.service';
import { OauthService } from '../../services/oauth/oauth.service';
import { Credential } from '../../models/credential';
import { GLOBAL } from '../../services/enviroment/global';

@Component({
  selector: 'app-landing-page',
  templateUrl: './landing-page.component.html',
  styleUrls: ['./landing-page.component.sass'],
  providers:[ValidationsService,UserService,OauthService]
})
export class LandingPageComponent implements OnInit {
  public smallResolution: Observable<boolean>;
  private stateContactLayer:string = 'large';
  @ViewChild('email') email:ElementRef;
  @ViewChild('usernameLogin') usernameLogin:ElementRef;
  @ViewChild('emailToRestore') emailToRestore:ElementRef;
  @ViewChild('passwordLogin') passwordLogin:ElementRef;
  public terms:boolean;
  public emailValid:boolean;
  public termsValid:boolean;
  private key:string;
  private credential:Credential;
  private validLogin:any;
  constructor(
    private _validationsService:ValidationsService,
    private _route:ActivatedRoute,
    private _router:Router,
    private _userService:UserService,
    private _oauthService:OauthService
  ) {
    this.emailValid=true;
    this.termsValid=true;
    this.terms=false;
    this.credential=new Credential(GLOBAL.userAdmin,GLOBAL.passwodrAdmin);
    this.validLogin={
      user:true,
      pass:true,
      emailRestoreValid:true
    }
  }
  ngOnInit() {
    /*if(typeof(Storage)!="undefined"){
        if(localStorage.getItem("key")!=null){
          localStorage.removeItem("key");
        }
        this.authentication();
      }else{
        alert("Update your navigator");
      }*/
    $('.modal').modal();
    $(".button-collapse").sideNav({
      closeOnClick: true
    });
  }
  authentication(){
    this._oauthService.login(this.credential).subscribe(
      success=>{
         this.key=success.token;
         if(this._oauthService.setLocalStorageKey(this.key)){
         }
      },
      error=>{
        console.log(error);
       // location.reload();
      }
    );
  }
  toInfo(){
    $('html, body').animate({
                    scrollTop: $("#infoRedzza").offset().top
                }, 1000);
  }
  getModalSingUp(){
     $('#modal-signup').modal('open');
  }
  getModalLogIn(){
     $('#modal-login').modal('open');
  }
  getModalTerms(){
    $('#modal-terms').modal('open');
 }
  closeModalLogIn(){
    $('#modal-login').modal('close');
 }
  getModalRestore(){
    $('#modal-login').modal('close');
    $('#modal-restore-password').modal('open');
 }
 getModalRegister(){
  $('#modal-login').modal('close');
  $('#modal-signup').modal('open');
}
  singIn(){
    if(this.validateData()){
      this._userService.validateEMail(this.email.nativeElement.value).subscribe(
        success=>{
          if(success.exists){
            var $toastContent = $('<span>El correo eléctronico <br> ingresado ya esta registrado,<br> por favor inicie sesión o <br>ingrese uno diferente</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
            Materialize.toast($toastContent, 10000);
          }else{
            $('#modal-signup').modal('close');
            this.toStepOne();
          }
        },
        error=>{
          console.log(error);
        }
      );
    }
    
  }
  toStepOne(){
    this._router.navigate(['/registeruserone',this.email.nativeElement.value]);
  }
  validateData(){
    this.termsValid=true;
    this.emailValid=true;
    let success=true;
    if(!this._validationsService.validateEmail(this.email.nativeElement.value)){
      this.emailValid=false;
      success=false;
    }
    if(!this.terms){
      this.termsValid=false;
      success=false;
    }
    return success;
  }
  validDataLogIn(){
    this.validLogin={
      user:true,
      pass:true
    }
    let success=true;
    if(this.usernameLogin.nativeElement.value.length==0){
      this.validLogin.user=false;
      success=false;
    }
    if(this.passwordLogin.nativeElement.value.length==0){
      this.validLogin.pass=false;
      success=false;
    }
    return success;
  }
  logIn(){
    if(this.validDataLogIn()){
      this.credential.user=this.usernameLogin.nativeElement.value;
      this.credential.password=this.passwordLogin.nativeElement.value;
      this._oauthService.login(this.credential).subscribe(
        success=>{
          if(this._oauthService.setLocalStorageKey(success.token)){
            if(this._userService.setStorageUserId(success.user[0].pk)){
              this.closeModalLogIn();
              this.toLobby();
            }
          }
        },
        error=>{
          if(error.status==422){
            var $toastContent = $('<span>El correo eléctronico y  <br> la contraseña no cohinciden</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
            Materialize.toast($toastContent, 10000);
          }else{
            var $toastContent = $('<span>Error en la petición</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
            Materialize.toast($toastContent, 10000);
          }
        }
      );
    }
  }
  toLobby(){
    this._router.navigate(['/lobby/wall']);
  }
  restorePassword(){
    if(this.emailToRestore.nativeElement.value.length==0){
      this.validLogin.emailRestoreValid=false;
    }else{
      this._oauthService.restorePassword(this.emailToRestore.nativeElement.value).subscribe(
        success=>{
          console.log(success);
          var $toastContent = $('<span>Te hemos enviado un mail para recuperar tu contraseña</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 6000);
        },
        error=>{
          alert("Error en la petición");
        }
      );
    }
    
  }

}



