import { Component, OnInit, ViewChild } from '@angular/core';
import { UserService } from '../../../services/user/user.service';
import { Category } from '../../../models/category';
import { CategoryService } from '../../../services/category/category.service';
import { DropzoneDirective, DropzoneConfigInterface } from '../../../../../node_modules/ngx-dropzone-wrapper';
import { GLOBAL }  from '../../../services/enviroment/global';
import { OauthService } from '../../../services/oauth/oauth.service';

@Component({
  selector: 'app-profile-other-user',
  templateUrl: './profile-other-user.component.html',
  styleUrls: ['./profile-other-user.component.sass'],
  providers:[UserService,CategoryService,OauthService]
})
export class ProfileOtherUserComponent implements OnInit {
  private user:any;
  private profile:any;
  private numFollowers:number;
  private daysJoined:number;
  private haveCategories:any[];//las que tiene el usuario
  private searchCategories:any[];//las que busca
  private categoriesToHave:Category[];//las que tiene el sistema
  private categoriesToSearch:Category[];//las que tiene el sistema
  private usId:number;
  public configImg: DropzoneConfigInterface;
  @ViewChild(DropzoneDirective) directiveRef: DropzoneDirective;
  constructor(
    private _userService:UserService,
    private _categoryService:CategoryService,
    private _oauthService:OauthService
  ) { 
    this.user={};
    this.profile={};
    this.daysJoined=0;
    this.numFollowers=0;
    this.haveCategories=[];
    this.searchCategories=[];
  }

  ngOnInit() {    
    this.getUser();
  }
  
  getUser(){
    this.usId=parseInt(this._userService.getStorageOtherUserId());
    if(this.usId){
      this._userService.getUser(this.usId).subscribe(
        success=>{
          console.log(success.data);
          this.user=success.data.user[0].fields;
          this.profile=success.data.profile[0].fields;
          this.numFollowers=success.data.numberFollowers;
          this.haveCategories=success.data.haveCategories;
          console.log(this.haveCategories);
          this.searchCategories=success.data.searchCategories;
          this.determinateDaysFromSingIn(this.user.date_joined);
        },
        error=>{
          alert("Request error")
        }
      );
    }
  }
  determinateDaysFromSingIn(date:any){
    var dateNow = new Date();
    var dateJoined= new Date(date);
    var timeDiff = Math.abs(dateNow.getTime() - dateJoined.getTime());
    var diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24)); 
    this.daysJoined=diffDays;
  }

}



