import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { Router, ActivatedRoute, Params } from '@angular/router';
import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/observable';
import { Credential } from '../../models/credential';

@Injectable()
export class OauthService {
  constructor(
    private _router:Router,
    private _http: Http
    ){}
// Pide token a api oauth2 de Hive AG
// @Autor: Camilo Cano 
// @Params: un objeto credential con username y password
// @Return: json con token y sus atributos correspondientes
login(credentials:Credential){
  let urloAuth2= 'api/v1/apiServices/loginUser/';
  return this._http.post(urloAuth2 ,credentials,{}).map(res=>res.json());
}
  // Almacena key en local storage
  // @Autor: Camilo Cano 
  // Params: string para almacenar
  // Return: boolean
  setLocalStorageKey(key:string){
    let storageSuccess:boolean = true;
    if (typeof(Storage) !== "undefined") {
        if(key!=null && key!=undefined){
          localStorage.removeItem("key");
          localStorage.setItem("key", key);
        }else{
          storageSuccess=false;
        }
    }else {
        alert("Update your navigator");
        storageSuccess=false;
    }
    return storageSuccess;
  }
  // Recuperar key de local storage
  // @Autor: Camilo Cano 
  // Return: token almacenado 
  getLocalStorageKey():string{
    console.log("getStorageToken: oauth2.services");
    let key:string="";
    if(typeof(Storage)!="undefined"){
       key=localStorage.getItem("key");
    }else {
       key=null;
        console.log("Local Storage doesnt support");
    }
    return key;
  }
  // Hace logOut, elimina el key y redirige a la raiz de la app web
  // @Autor: Camilo Cano 
  logOut(){
    return this._http.post('rest-auth/logout/',{}).map(res=>res.json()).subscribe(
      success=>{
        if(typeof(Storage)!="undefined"){
          if(localStorage.getItem("usId")!=null){
            localStorage.removeItem("usId");
          }
          if(localStorage.getItem("key")!=null){
            localStorage.removeItem("key");
          }
        }else{
          alert("Update your navigator");
        }
        this._router.navigate(['/']);
      },
      error=>{
        if(typeof(Storage)!="undefined"){
          if(localStorage.getItem("usId")!=null){
             localStorage.removeItem("usId");
          }
          if(localStorage.getItem("key")!=null){
            localStorage.removeItem("key");
          }
          this._router.navigate(['/']);
        }else{
          alert("Update your navigator");
        }
      }
    );
  }
  sessionOpen(){
    let log=true;
    if(typeof(Storage)!="undefined"){
      if(localStorage.getItem("usId")==null){
        log=false;
      }
      if(localStorage.getItem("key")==null){
        log=false;
      }
      if(!log){
        this._router.navigate(['/']);
      }
    }
    return log;
  }
  // recuperar password
  restorePassword(emailParam:string){
    let emailFormat={
      email:emailParam
    }
    return this._http.post('rest-auth/password/reset/',emailFormat,{}).map(res=>res.json());
  }
  // recuperar password
  changeRestorePassword(emailParam:string){
    let emailFormat={
      email:emailParam
    }
    return this._http.post('rest-auth/password/reset/',emailFormat,{}).map(res=>res.json());
  }
}





