import { Component, OnInit,ElementRef,ViewChild   } from '@angular/core';
import { UserService } from '../../../services/user/user.service';
import { ValidationsService } from '../../../services/validations/validations.service';
import { Place } from '../../../models/place';
import { PlaceService } from '../../../services/place/place.service';
import { OauthService } from '../../../services/oauth/oauth.service';

@Component({
  selector: 'app-my-personal-data',
  templateUrl: './my-personal-data.component.html',
  styleUrls: ['./my-personal-data.component.sass'],
  providers:[UserService,ValidationsService,PlaceService,OauthService]
})
export class MyPersonalDataComponent implements OnInit {
  private user:any;
  private profile:any;
  private validData:any;
  @ViewChild('email') email:ElementRef;
  @ViewChild('userEdit') userEdit:ElementRef;
  @ViewChild('newPass') newPass:ElementRef;
  @ViewChild('confirmPass') confirmPass:ElementRef;
  @ViewChild('currentPass') currentPass:ElementRef;
  @ViewChild('phone') phone:ElementRef;
  @ViewChild('biography') biography:ElementRef;  
  private titleDataEdit:string;
  private usId:number;
  public places:Place[];
  public placeId:number;
  constructor(
    private _userService:UserService,
    private _validationsService:ValidationsService,
    private _placeService:PlaceService,
    private _oauthService:OauthService
  ) { 
    this.user={};
    this.user.email="";
    this.user.username="";
    this.user.first_name="";
    this.user.last_name="";
    this.profile={};
    this.profile.gender="";
    this.profile.birth_date="01/01/20";
    this.profile.biography="";
    this.validData={
      email:true,
      user:true,
      validData:true,
      confirmPass:true,
      currentPass:true,
      phone:true,
      biography:true
    }
    this.titleDataEdit="";
  }
  ngOnInit() {
    this.initiModals();
    this.getUser();
  }
  modalEmail(){
    $('#modal-email').modal('open');
  }
  modalUser(){
    $('#modal-user').modal('open');
  }
  modalPassword(){
    $('#modal-password').modal('open');
  }
  modalPhone(){
    $('#modal-phone').modal('open');
  }
  modalLocation(){
    $('#modal-location').modal('open');
  }
  modalBiographi(){
    $('#modal-biographi').modal('open');
  }
  initiModals(){
    $('.modal').modal();
  }
  closeModalEmail(){
    $('#modal-email').modal('close');
    $('#modal-user').modal('close');
    $('#modal-password').modal('close');
    $('#modal-phone').modal('close');
    $('#modal-location').modal('close');
    $('#modal-biographi').modal('close');
  }
  // obtener usuario
  getUser(){
    this.usId=parseInt(this._userService.getStorageUserId());
      if(this.usId){
      this._userService.getUser(this.usId).subscribe(
        success=>{
          console.log(success.data);
          this.user=success.data.user[0].fields;
          this.profile=success.data.profile[0].fields;
          if(this.profile.gender=="M"){
            this.profile.gender="Masculino";
          }else{
            this.profile.gender="Femenino"
          }
          if(this.profile.phone==""){
            this.profile.phone="No ingresado"
          }
          if(this.profile.biography==""){
            this.profile.biography="No ingresado"
          }
          this.getPlaces();
        },
        error=>{
          alert("Request failed")
        }
      );
    }
  }
  //actualizar email
  updateEMail(){
    if(this.validDataEMail()){
      let dataEMail={
        email:this.email.nativeElement.value
      }
      this.titleDataEdit="e-mail";
      this.updateData(dataEMail);
    }
  }
  validDataEMail(){
    this.validData={
      email:true
    }
    let success=true;
    if(!this._validationsService.validateEmail(this.email.nativeElement.value)){
      this.validData.email=false;
      success=false;
    }
    return success;
  }
  //acrualizar usuario
  updateUser(){
    if(this.validDataUser()){
      let dataUser={
        username:this.userEdit.nativeElement.value
      }
      this.titleDataEdit="usuario";
      this.updateData(dataUser);
    }
  }
  validDataUser(){
    debugger;
    this.validData={
      user:true
    }
    let success=true;
    if(String(this.userEdit.nativeElement.value).length<2){
      this.validData.user=false;
      success=false;
    }
    return success;
  }
  //actualizar password
  updatePassword(){
    if(this.validDataPassword()){
      let dataPassword={
        new_password1:this.newPass.nativeElement.value,
        new_password2:this.confirmPass.nativeElement.value,
        old_password:this.currentPass.nativeElement.value
      }
      this.titleDataEdit="contraseña";
      this.updateDataPassword(dataPassword);
    }
  }
  validDataPassword(){
    debugger;
    this.validData={
      newPass:true,
      confirmPass:true,
      currentPass:true
    }
    let success=true;
    if(String(this.currentPass.nativeElement.value).length<1){
      this.validData.currentPass=false;
      success=false;
    }
    if(!this._validationsService.validatePassword(this.newPass.nativeElement.value)){
      this.validData.newPass=false;
      success=false;
    }
    if(this.newPass.nativeElement.value!=this.confirmPass.nativeElement.value){
      this.validData.confirmPass=false;
      success=false;
    }
    return success;
  }
  //actualizar cualquier dato
  updateData(data:any){
    this._userService.updateUser(data).subscribe(
      success=>{
        this.getUser();
        var $toastContent = $('<span>Actualizacion exitosa</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
        Materialize.toast($toastContent, 5000);
        this.closeModalEmail();
      },
      error=>{
        if(error.status==422){
          var $toastContent = $('<span>El '+this.titleDataEdit+' ya está registrado</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 5000);
        }else{
          var $toastContent = $('<span>Error en la petición</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 5000);
        }
      }
    );
  }
  //actualizar password
  updateDataPassword(data:any){
    this._userService.changePassword(data).subscribe(
      success=>{
        this.getUser();
        var $toastContent = $('<span>Contraseña actualizada</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
        Materialize.toast($toastContent, 5000);
        this.closeModalEmail();
      },
      error=>{
        debugger;
        if(error.status==400){
          var $toastContent = $('<span>La '+this.titleDataEdit+' actual es incorrecta</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 5000);
          this.validData.currentPass=false;
        }else{
          var $toastContent = $('<span>La '+this.titleDataEdit+' no pudo ser actualizada</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 5000);
        }
        
        console.log(error);
      }
    );
  }
  //actualizar cumpleaños
  changeBirthDay(dateParam:any){
    console.log(this.profile.birth_date);
    let dataBirthday={
      birth_date:this.profile.birth_date
    }
    this.titleDataEdit="cumpleaños";
    this.updateData(dataBirthday);
  }
  //actualizar genero
  updateGenre(){
    let dataGenre={
      gender:this.profile.gender
    }
    this.titleDataEdit="género";
    this.updateData(dataGenre);
  }
  //actualizar telefono
  updatePhone(){
    if(this.validDataPhone()){
      let dataPhone={
        phone:this.phone.nativeElement.value
      }
      this.titleDataEdit="teléfono";
      this.updateData(dataPhone);
    }
  }
  validDataPhone(){
    this.validData={
      phone:true
    }
    let success=true;
    if(!this._validationsService.validatePhone(this.phone.nativeElement.value)){
      this.validData.phone=false;
      success=false;
    }
    return success;
  }
  //actualizar biografia
  updateBiography(){
    if(this.validDataBiography()){
      let dataBiography={
        biography:this.biography.nativeElement.value
      }
      this.titleDataEdit="biografia";
      this.updateData(dataBiography);
    }
  }
  validDataBiography(){
    this.validData={
      biography:true
    }
    let success=true;
    if(this.biography.nativeElement.value=="" || String(this.biography.nativeElement.value).length > 121){
      this.validData.biography=false;
      success=false;
    }
    return success;
  }
  //actualiza ciudad
  updatePlaceUser(){
    let dataPlace={
      location:this.placeId
    }
    this.updateData(dataPlace);
  }
  //obtener ciudades
  getPlaces(){
    this._placeService.getPlaces().subscribe(
      success=>{
        this.places=success;
        console.log(this.places);
        this.placeId=this.profile.location;
      },
      error=>{
        alert("Request Error");
      }
    );
  }
}



