import { Component, OnInit,ElementRef,ViewChild  } from '@angular/core';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { ValidationsService } from '../../services/validations/validations.service';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.sass'],
  providers:[ValidationsService]
})
export class ChangePasswordComponent implements OnInit {
 private token:string;
 private uid:string;
 @ViewChild('password') password:ElementRef;
 @ViewChild('passwordRepeat') passwordRepeat:ElementRef;
 private validateData:any;
  constructor(
    private _route:ActivatedRoute,
    private _router:Router,
    private _validationsService:ValidationsService
  ) { 
    this.validateData={
      password:true,
      passwordRepeat:true
    }
  }

  ngOnInit() {
    this._route.params.forEach((params:Params)=>{
      this.token=params["token"];
      this.token=params["uid"];
      console.log(this.token+", "+this.uid);
    });
  }
  updatePassword(){
    if(this.validDataPassword()){
      let dataPassword={
        new_password1:this.password.nativeElement.value,
        new_password2:this.passwordRepeat.nativeElement.value,
        token:this.token
      }
      console.log(dataPassword);
      //this.updateDataPassword(dataPassword);
    }
  }
  validDataPassword(){
    debugger;
    this.validateData={
      password:true,
      passwordRepeat:true
    }
    let success=true;
    if(!this._validationsService.validatePassword(this.password.nativeElement.value)){
      this.validateData.password=false;
      success=false;
    }
    if(this.password.nativeElement.value!=this.passwordRepeat.nativeElement.value){
      this.validateData.passwordRepeat=false;
      success=false;
    }
    return success;
  }
  

}



