import { Injectable } from '@angular/core';

@Injectable()
export class ValidationsService {

  constructor() { }

  validateSomeName(str:string){
    let validate=true;
    var regexp = new RegExp("[^0-9@#%&/()¿¡!]*[a-zA-Zñ ÑáéíóúüàèìòùÁÉÍÓÚÜÀÈÌÒÙ]+[^0-9@#%&/()¿¡!]*$");
    if(str.length>20 || str.length<2){
      validate=false;
    }else{
      validate= regexp.test(str);
    }
    return validate;
  }
  validatePassword(str:string){
    if(str.length>5){
      return true;
    }else{
      return false;
    }
  }
  validateEmail(str:string) {
    var regexp = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/;
    return regexp.test(str);
  }
  validatePhone(str:number){
    var strPhone=String(str);
    var regexp = /^[0-9]{10,15}$/;
    return regexp.test(strPhone);
  }
  validateImei(str:number){
    var strImei=String(str);
    var regexp = /^[0-9]{15}$/;
    return regexp.test(strImei);
  }
  validateOneNumber(str:string){
    var regexp = /^[0-9]{1}$/;
    return regexp.test(str);
  }
}



