import { Component, OnDestroy, OnInit, Renderer } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription } from 'rxjs/Subscription';
import { OauthService } from '../../../../services/oauth/oauth.service';
import { Category } from '../../../../models/category';
import { CategoryService } from '../../../../services/category/category.service';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { User } from '../../../../models/user';
@Component({
  selector: 'app-registration-step-two',
  templateUrl: './registration-step-two.component.html',
  styleUrls: ['./registration-step-two.component.sass'],
  providers : [OauthService,CategoryService]
})
export class RegistrationStepTwoComponent implements OnInit {
  private key:string;
  private categories:Category[];
  public selectMultipleValues:any[];
  public categoryValid:boolean;
  public user:User;
  constructor(
    private _oauthService:OauthService,
    private _categoryService:CategoryService,
    private _route:ActivatedRoute,
    private _router:Router
  ) {
    this.selectMultipleValues=[];
    this.categoryValid=true;
    this.user = new User();
    this.user.i_search=[];
  }
  ngOnInit() {
    if(this.validProgress()){
      this.getCategories();
    }
    
  }
  getCategories(){
    this._categoryService.getCategories().subscribe(
      success=>{
        this.categories=success;
        console.log(this.categories);
      },
      error=>{
        alert("Request Error");
      }
    );
  }
  validateStepTwo(){
    if(this.validData()){
      this.storageOnSession();
    }
    console.log(this.selectMultipleValues);
  }
  validData(){
    let succes=true;
    if(this.selectMultipleValues.length==0){
      this.categoryValid=false;
      succes=false;
    }
    if(this.selectMultipleValues.length>5){
      succes=false;
      var $toastContent = $('<span>No puedes seleccionar mas de 5 categorías</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
      Materialize.toast($toastContent, 10000);
    }
    return succes;
  }
  storageOnSession(){
    if(typeof(Storage)){
      this.user=JSON.parse(sessionStorage.getItem('register'));
      if(this.user){
        this.user.i_search=this.selectMultipleValues;
        sessionStorage.setItem('register',JSON.stringify(this.user));
        this.toStepThree();
      }
    }
  }
  toStepThree(){
      this._router.navigate(['/registeruserthree']);
  }
  validProgress(){
    let success=true;
    if(typeof(Storage)){
      this.user=JSON.parse(sessionStorage.getItem('register'));
      if(this.user){
        if(this.user.first_name==undefined || this.user.last_name==undefined || this.user.password==undefined){
          this._router.navigate(['/registeruserone']);
        }
      }else{
        this._router.navigate(['/registeruserone']);
      }
    }
    return success;
  }
}



