import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { User } from '../../models/user';
import 'rxjs/add/operator/map';

@Injectable()
export class UserService {

  constructor(
    private _http:Http
  ) { }
  validateEMail(emailParam:string){
    let email={
      email:emailParam
    }
     return this._http.post('api/v1/apiServices/validateEmail/',email,{}).map(res=>res.json());
  }
  saveUser(user:User){
    return this._http.post('api/v1/apiServices/createUser/',user,{}).map(res=>res.json());
  }
  getUser(usId:number){
    return this._http.get('api/v1/users/'+usId+'/getData/',{}).map(result=>result.json());
  }
  updateUser(dataUser:any){
    return this._http.put("api/v1/apiServices/updateUser/",dataUser,{});
  }
  changePassword(password:any){
    return this._http.post('rest-auth/password/change/',password,{}).map(res=>res.json());
  }
    // Almacena key en local storage
  // @Autor: Camilo Cano 
  // Params: string para almacenar
  // Return: boolean
  setStorageUserId(usId:string){
    let storageSuccess:boolean = true;
    if (typeof(Storage) !== "undefined") {
        if(usId!=null && usId!=undefined){
          localStorage.removeItem("usId");
          localStorage.setItem("usId", usId);
        }else{
          storageSuccess=false;
        }
    }else {
        storageSuccess=false;
        alert("Update your navigator");
    }
    return storageSuccess;
  }
  // Recuperar key de local storage
  // @Autor: Camilo Cano 
  // Return: token almacenado 
  getStorageUserId():string{
    console.log("getStorageToken: oauth2.services");
    let usId:string="";
    if(typeof(Storage)!="undefined"){
      usId=localStorage.getItem("usId");
    }else {
      usId=null;
      alert("Update your navigator")
    }
    return usId;
  }
  setStorageOtherUserId(usId:string){
    let storageSuccess:boolean = true;
    if (typeof(Storage) !== "undefined") {
        if(usId!=null && usId!=undefined){
          localStorage.removeItem("usOId");
          localStorage.setItem("usOId", usId);
        }else{
          storageSuccess=false;
        }
    }else {
        storageSuccess=false;
        alert("Update your navigator");
    }
    return storageSuccess;
  }
  // Recuperar key de local storage
  // @Autor: Camilo Cano 
  // Return: token almacenado 
  getStorageOtherUserId():string{
    console.log("getStorageToken: oauth2.services");
    let usId:string="";
    if(typeof(Storage)!="undefined"){
      usId=localStorage.getItem("usOId");
    }else {
      usId=null;
      alert("Update your navigator")
    }
    return usId;
  }
}



