import { Component, OnInit, NgZone } from '@angular/core';
import { UserService } from '../../../services/user/user.service';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { OauthService } from '../../../services/oauth/oauth.service';
import { NoticesService } from '../../../services/notices/notices.service';

@Component({
  selector: 'app-my-stand',
  templateUrl: './my-stand.component.html',
  styleUrls: ['./my-stand.component.sass'],
  providers:[OauthService,NoticesService]
})
export class MyStandComponent implements OnInit {
  private usId:number;
  private changeConten:boolean;
  private currentOption:string;
  private itemsH:any[];
  private itemsS:any[];
  private idDeleteNotices:number;
  constructor(
    private _route:ActivatedRoute,
    private _router:Router,
    private _oauthService:OauthService,
    private _noticesService:NoticesService,
    private zone:NgZone
  ) { 
    this.usId=0;
    this.changeConten=true;
    this.currentOption="H";
    this.itemsH=[];
    this.itemsS=[];
    this.idDeleteNotices=0;
  }
  ngOnInit() {
    this.initiModals();
    this.getUserId();
  }
  initiModals(){
    $('.modal').modal();
  }
  modalNewNotice(){
    $('#modal-publication').modal('open');
  }
  toNewItem(type:string){
    $("#modal-publication").modal("close");
    this._router.navigate(['/dashboard/newitem',type]);
  }
  getUserId(){
    if(typeof(Storage)){
      this.usId=parseInt(localStorage.getItem("usId"));
      this.getItems();
    }
  }
  getItems(){
    this._noticesService.getNotices(this.usId).subscribe(
      success=>{
        this.zone.run(() => {
          for(var i=0;i<success.data.length;i++){
            if(success.data[i].kind=="i_have"){
              this.itemsH.push(success.data[i]);
            }else{
              this.itemsS.push(success.data[i]);
            }
          }
          console.log(this.itemsH);
          console.log(this.itemsS);
        });
        },
      error=>{
        console.log(error);
      }
    );
  }
  changeContent(optionPanel:string){
    if(optionPanel!=this.currentOption){
      if(this.changeConten){
        this.changeConten=false;
      }else{
        this.changeConten=true;
      }
      this.currentOption=optionPanel;
    }
  }
  beginDelete(idNotices:number,event:any){
    event.stopPropagation();
    this.idDeleteNotices=idNotices;
   $('#modal-delete-confirm').modal("open"); 
  }
  aceptDelete(){
    this._noticesService.deleteNotices(this.idDeleteNotices).subscribe(
      success=>{
        if(success){
          var $toastContent = $('<span>Publicacion eliminada</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 10000);
          this.itemsH=[];
          this.itemsS=[];
          this.getItems();
          this.cancelDeleteUser();
        }
      },
      error=>{
        var $toastContent = $('<span>Error en la petición</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
        Materialize.toast($toastContent, 10000);
        console.log(error);
      }
    );
  }
  cancelDeleteUser(){
    $("#modal-delete-confirm").modal("close");
  }
  getNotices(ntId:number){
    this._router.navigate(['dashboard/viewnotices',ntId]);
  }
}



