import { Component, OnInit,ViewChild } from '@angular/core';
import { DropzoneDirective, DropzoneConfigInterface } from '../../../../../node_modules/ngx-dropzone-wrapper';
import { OauthService } from '../../../services/oauth/oauth.service';
import { GLOBAL }  from '../../../services/enviroment/global';
import { Router, Params, ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-updates-resources',
  templateUrl: './updates-resources.component.html',
  styleUrls: ['./updates-resources.component.sass'],
  providers:[OauthService]
})
export class UpdatesResourcesComponent implements OnInit {
  @ViewChild(DropzoneDirective) directiveRef: DropzoneDirective;
  public configMainImg: DropzoneConfigInterface;
  public configSecundaryImg: DropzoneConfigInterface;
  public configVideo: DropzoneConfigInterface;
  public ntId:number;
  private key:string;
  private canSave:boolean;
  constructor(
    private _oauthService:OauthService,
    private _router:Router
  ) {
    this.canSave=false;
  }
  ngOnInit() {
    this.initConfig();
    if(typeof(Storage)){
      this.ntId=parseInt(sessionStorage.getItem("pkNotice"));
    }else{
      var $toastContent = $('<span>Update your navigator</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
      Materialize.toast($toastContent, 10000);
    }
  }

  onUploadError(any:any){
    console.log(any);
    if(any.status==401){
      //this._router.navigate(['/']);
    }
  }
  onUploadSuccess(any){
    var $toastContent = $('<span>Imagen almacenada exitosamente</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
    Materialize.toast($toastContent, 10000);
    console.log(any);
    //this.iconUpload=false;
    //this.reset();
    this.canSave=true;
  }
  initConfig(){
    let key;
    if(typeof(Storage) !== "undefined") {
      key=localStorage.getItem("key");
      this.configMainImg= {
        url: GLOBAL.apiGeepy+"api/v1/images/",
        paramName: "image",
        thumbnailWidth: 265,
        thumbnailHeight: 195,
        resizeHeight: 400,
        resizeWidth: 300,
        method:"POST",
        acceptedFiles: "image/jpeg,image/png,image/jpg",
        headers: {
          'Cache-Control': null,
          'X-Requested-With': null,
          'Authorization': 'Token '+key
        },
        maxFilesize: 2,
      };
      this.configSecundaryImg={
        url: GLOBAL.apiGeepy+"api/v1/images/",
        paramName: "image",
        thumbnailWidth: 145,
        thumbnailHeight: 95,
        resizeHeight: 1000,
        resizeWidth: 330,
        method:"POST",
        acceptedFiles: "image/jpeg,image/png,image/jpg",
        headers: {
          'Cache-Control': null,
          'X-Requested-With': null,
          'Authorization': 'Token '+key
        },
        maxFilesize: 2,
      }
      this.configVideo={
        url: GLOBAL.apiGeepy+"api/v1/videos/",
        paramName: "video",
        thumbnailWidth: 245,
        thumbnailHeight: 245,
        resizeHeight: 245,
        resizeWidth: 245,
        method:"POST",
        headers: {
          'Cache-Control': null,
          'X-Requested-With': null,
          'Authorization': 'Token '+key
        },
        acceptedFiles:'.mp4',
        maxFilesize: 100,
      }
    }
  }
  sending(anyP:any){
    anyP[2].append("notice",String(this.ntId));
   //anyP[2].notice=String(this.ntId);
    console.log(anyP[2]);
  }
  finisht(){
    if(this.canSave){
      var $toastContent = $('<span>Publicación realizada exitosamente</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
      Materialize.toast($toastContent, 10000);
      setTimeout(()=>{this._router.navigate(['dashboard/mystand']);},200);
    }else{
      var $toastContent = $('<span>Debes subir al menos una imagen</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
      Materialize.toast($toastContent, 10000);
      
    }
    
  }
}



