import { Injectable } from '@angular/core';
import {Http} from '@angular/http';
import {Notice} from '../../models/notice';

@Injectable()
export class NoticesService {

  constructor(private _http:Http) { }

  saveNotice(notice:Notice){
    return this._http.post('api/v1/apiServices/newNotice/',notice,{}).map(result=>result.json());
  }
  getNotices(usId:number){
    return this._http.get('api/v1/users/'+usId+'/getNotices/',{}).map(result=>result.json());
  }
  getNotice(ntId:number){
    return this._http.get('api/v1/notices/'+ntId+'/getData/',{}).map(result=>result.json());
  }
  deleteNotices(ntId:number){
    return this._http.delete('api/v1/notices/'+ntId,{}).map(result=>result.json());
  }
  updateNotices(data:any){
    return this._http.put('api/v1/apiServices/updateNotice/',data,{}).map(result=>result.json());
  }
  deleteImage(imgId:number){
    return this._http.delete('api/v1/images/'+imgId,{});
  }
  getWall(usId:number){
    return this._http.get('api/v1/apiServices/getHome/',{});
  }
  getSearch(data:any){
    return this._http.post('api/v1/apiServices/searchNotice/',data,{});
  }
}



