import { Component, OnInit, ViewChild,ElementRef } from '@angular/core';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { OauthService } from '../../services/oauth/oauth.service';
import { UserInterfaceService } from '../../services/user-interface/user-interface.service';
import { InboxService } from '../../services/inbox/inbox.service';

@Component({
  selector: 'app-lobby',
  templateUrl: './lobby.component.html',
  styleUrls: ['./lobby.component.sass'],
  providers:[OauthService,UserInterfaceService,InboxService]
})
export class LobbyComponent implements OnInit {
  private disabledFindS:boolean;
  private disabledFindH:boolean;
  private searchH:string;
  private searchS:string;
  private notificationsNumber:number;
  private showNotificationsNumber:boolean;
  constructor(
    private _route:ActivatedRoute,
    private _router:Router,
    private _oauthService:OauthService,
    private _userInterfaceService:UserInterfaceService,
    private _inboxService:InboxService
  ) { 
    this.searchH="";
    this.searchS="";
    this.showNotificationsNumber=false;
  }

  ngOnInit() {
    if(this._oauthService.sessionOpen()){
      this.initiModals();
    }
    this.disabledFindS=false;
    this.disabledFindH=false;
    $(".button-collapse").sideNav({
      closeOnClick: true
    });
    this.getNotifications();
  }
  initiModals(){
    $('.modal').modal();
  }
  toDashboard(){
    this._router.navigate(['/dashboard/myprofile']);
  }
  toCloseSession(){
    this._router.navigate(['/logout']);
  }
  toInbox(){
    this._router.navigate(['/dashboard/myinbox']);
  }
  modalNewNotice(){
    $('#modal-publication').modal('open');
  }
  modalSearch(){
    $('#modal-search').modal('open');
  }
  toNewItem(type:string){
    $("#modal-publication").modal("close");
    this._router.navigate(['/dashboard/newitem',type]);
  }
  usingHSearcher(text:string){
    if(text.length>0){
      this.disabledFindH=true;
    }else{
      this.disabledFindH=false;
    }
  }
  usingSSearcher(text:string){
    if(text.length>0){
      this.disabledFindS=true;
    }else{
      this.disabledFindS=false;
    }
  }
  searchItems(){
    let dataSearch={
      kind:0,
      title:""
    }
    if(!this.disabledFindS){
      dataSearch.kind=2;
      dataSearch.title=this.searchS;
    }else if(!this.disabledFindH){
      dataSearch.kind=1;
      dataSearch.title=this.searchH;
    }
    $('#modal-search').modal('close');
    this._userInterfaceService.sendSearcher(dataSearch);
  }
  getNotifications(){
    this._inboxService.getNotificationsNumber().subscribe(
      success=>{
        console.log(success);
        this.notificationsNumber=success.count;
        if(this.notificationsNumber>0){
          this.showNotificationsNumber=true;
        }
      },
      error=>{
        console.log(error);
      }
    );
  }


}



