import { Component, OnInit, ViewChild } from '@angular/core';
import { UserService } from '../../../services/user/user.service';
import { Category } from '../../../models/category';
import { CategoryService } from '../../../services/category/category.service';
import { DropzoneDirective, DropzoneConfigInterface } from '../../../../../node_modules/ngx-dropzone-wrapper';
import { GLOBAL }  from '../../../services/enviroment/global';
import { OauthService } from '../../../services/oauth/oauth.service';

@Component({
  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.sass'],
  providers:[UserService,CategoryService,OauthService]

})
export class MyProfileComponent implements OnInit {
  private user:any;
  private profile:any;
  private numFollowers:number;
  private daysJoined:number;
  private haveCategories:any[];//las que tiene el usuario
  private searchCategories:any[];//las que busca
  private categoriesToHave:Category[];//las que tiene el sistema
  private categoriesToSearch:Category[];//las que tiene el sistema
  private usId:number;
  public configImg: DropzoneConfigInterface;
  @ViewChild(DropzoneDirective) directiveRef: DropzoneDirective;
  constructor(
    private _userService:UserService,
    private _categoryService:CategoryService,
    private _oauthService:OauthService
  ) { 
    this.user={};
    this.profile={};
    this.daysJoined=0;
    this.numFollowers=0;
    this.haveCategories=[];
    this.searchCategories=[];
  }

  ngOnInit() {
    let key;
    if(typeof(Storage) !== "undefined") {
      key=localStorage.getItem("key");
      if ( key!=undefined && key!=null){
        this.configImg={
          url: GLOBAL.apiGeepy+"api/v1/apiServices/updateUser/",
          paramName: "avatar",
          thumbnailWidth: 245,
          thumbnailHeight: 245,
          resizeHeight: 245,
          resizeWidth: 245,
          method:"PUT",
          acceptedFiles: "image/jpeg,image/png,image/jpg",
          headers: {
            'Cache-Control': null,
            'X-Requested-With': null,
            'Authorization': 'Token '+key
          },
        };
      }
   }
    
    this.initiModals();
    this.getUser();
    this.getCategoriesH();
    this.getCategoriesS();
  }
  getCategoriesH(){
    this._categoryService.getCategories().subscribe(
      success=>{
        this.categoriesToHave=success;
        for(var i=0 ; i<this.categoriesToHave.length;i++){
          this.categoriesToHave[i].checked=false;
        }
        console.log(this.categoriesToHave);
        this.checkMyCategoriesH();
      },
      error=>{
        alert("Request Error");
      }
    );
  }
  getCategoriesS(){
    this._categoryService.getCategories().subscribe(
      success=>{
        this.categoriesToSearch=success;
        for(var i=0 ; i<this.categoriesToSearch.length;i++){
          this.categoriesToSearch[i].checked=false;
        }
        console.log(this.categoriesToSearch);
        this.checkMyCategoriesS();
      },
      error=>{
        alert("Request Error");
      }
    );
  }
  checkMyCategoriesH(){
    for(var i=0 ; i<this.haveCategories.length;i++){
      for(var j=0 ; j<this.categoriesToHave.length;j++){
        if(this.haveCategories[i].fields.category==this.categoriesToHave[j].id){
          this.categoriesToHave[j].checked=true;
        }
      }
    }
  }
  checkMyCategoriesS(){
    for(var ii=0 ; ii<this.searchCategories.length;ii++){
      for(var jj=0 ; jj<this.categoriesToSearch.length;jj++){
        if(this.searchCategories[ii].fields.category==this.categoriesToSearch[jj].id){
          this.categoriesToSearch[jj].checked=true;
        }
      }
    }
  }
  selectCategorySearch(data:any,category){
    /*debugger;
    let countChecked=0;
    if(data.target.checked){
      for(var jj=0 ; jj<this.categoriesToSearch.length;jj++){
        if(this.categoriesToSearch[jj].checked){
          console.log(this.categoriesToSearch[jj]);
          countChecked++;
          if(countChecked==5){

          }
        }
      }
    }
    if(data.target.checked){
      if(this.searchCategories.length<5){
        console.log("category:"+category+", "+data.target.checked);
        console.log(this.searchCategories);
        console.log(this.categoriesToSearch);
      }else{
        var $toastContent = $('<span>No puedes seleccionar mas de 5 categorías</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
        Materialize.toast($toastContent, 10000);
      }
    }*/

  }
  updateCategoriesSearch(){
    let countChecked=0;
    let canSave=true;
    let arrayToSave=[];
    for(var jj=0 ; jj<this.categoriesToSearch.length;jj++){
      if(this.categoriesToSearch[jj].checked){
        countChecked++;
        if(countChecked>5){
          var $toastContent = $('<span>No puedes seleccionar mas de 5 categorías</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 10000);
          canSave=false;
          break;
        }else{

          arrayToSave.push({pk:this.categoriesToSearch[jj].id});
        }
      }  
    }
    if(canSave){
      $('#modal-change-search-categories').modal('close');
      let dataUpdate={
        i_search:arrayToSave
      }
      this._userService.updateUser(dataUpdate).subscribe(
        success=>{
          if(success.status==200){
            var $toastContent = $('<span>Categorias que busco actualizadas</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
            Materialize.toast($toastContent, 5000);
            this.getUser();
            this.getCategoriesH();
            this.getCategoriesS();
          }
        },
        error=>{
          var $toastContent = $('<span>Error en la petición</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 5000);
          console.log(error);
        }
      );
    }
  }
  updateCategoriesHave(){
    let countChecked=0;
    let canSave=true;
    let arrayToSave=[];
    for(var jj=0 ; jj<this.categoriesToHave.length;jj++){
      if(this.categoriesToHave[jj].checked){
        countChecked++;
        if(countChecked>5){
          var $toastContent = $('<span>No puedes seleccionar mas de 5 categorías</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 6000);
          canSave=false;
          break;
        }else{
          arrayToSave.push({pk:this.categoriesToHave[jj].id});
        }
      }
    }
    if(canSave){
      $('#modal-change-have-categories').modal('close');
      let dataUpdate={
        i_have:arrayToSave
      }
      this._userService.updateUser(dataUpdate).subscribe(
        success=>{
          if(success.status==200){
            var $toastContent = $('<span>Categorias que tengo actualizadas</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
            Materialize.toast($toastContent, 5000);
            this.getUser();
            this.getCategoriesH();
            this.getCategoriesS();
          }
        },
        error=>{
          var $toastContent = $('<span>Error en la petición</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 5000);
          console.log(error);
        }
      );
      console.log(arrayToSave);
    }
  }
  initiModals(){
    $('.modal').modal();
  }
  modalChangeImage(){
    $('#modal-change-image').modal('open');
  }
  modalChangeCategriesIHave(){
    $('#modal-change-have-categories').modal('open');
  }
  modalChangeCategriesISearch(){
    $('#modal-change-search-categories').modal('open');
  }
  getUser(){
    this.usId=parseInt(this._userService.getStorageUserId());
    if(this.usId){
      this._userService.getUser(this.usId).subscribe(
        success=>{
          console.log(success.data);
          this.user=success.data.user[0].fields;
          this.profile=success.data.profile[0].fields;
          this.numFollowers=success.data.numberFollowers;
          this.haveCategories=success.data.haveCategories;
          console.log(this.haveCategories);
          this.searchCategories=success.data.searchCategories;
          this.determinateDaysFromSingIn(this.user.date_joined);
        },
        error=>{
          alert("Request error")
        }
      );
    }
  }
  determinateDaysFromSingIn(date:any){
    var dateNow = new Date();
    var dateJoined= new Date(date);
    var timeDiff = Math.abs(dateNow.getTime() - dateJoined.getTime());
    var diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24)); 
    this.daysJoined=diffDays;
  }
  onUploadError(any:any){
    console.log(any);
    if(any.status==401){
      //this._router.navigate(['/']);
    }
  }
  onUploadSuccess(any){
    console.log(any);
    var $toastContent = $('<span>Imagen de perfil actualizada</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
    Materialize.toast($toastContent, 5000);
    $('#modal-change-image').modal('close');
    this.getUser();
    this.reset();
  }
  reset(){
    this.directiveRef.reset();
  }

}



