import {XHRBackend, Http, RequestOptions} from '@angular/http';
import {InterceptedHttpService} from './intercepted-http.service';
import { Router, Params, ActivatedRoute } from '@angular/router';


export function IntercepterHttpFactory(xhrBackend: XHRBackend, requestOptions: RequestOptions,router:Router): Http {
    return new InterceptedHttpService(xhrBackend, requestOptions,router);
}


