import { Component, OnInit } from '@angular/core';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { OauthService } from '../../services/oauth/oauth.service';
import { UserService } from '../../services/user/user.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.sass'],
  providers:[OauthService,UserService]
})
export class DashboardComponent implements OnInit {
  private optionMenu:any;
  private profile:any;
  private user:any;
  private usId:number;
  constructor(
    private _route:ActivatedRoute,
    private _router:Router,
    private _oauthService:OauthService,
    private _userService:UserService
  ) { 
    this.optionMenu={
      profile:true,
      stand:false,
      inbox:false,
      personalData:false
    }
    this.profile={};
    this.user={};
  }
  resetMenu(){
    this.optionMenu={
      profile:false,
      stand:false,
      inbox:false,
      personalData:false
    }
  }
  ngOnInit() {
    $(".button-collapse").sideNav({
      closeOnClick: true
    });
    this.getUser();
  }
  toPersonalData(){
    this.resetMenu();
    this.optionMenu.personalData=true;
    this._router.navigate(['/dashboard/mydatapersonal']);
  }
  toProfile(){
    this.resetMenu();
    this.optionMenu.profile=true;
    this._router.navigate(['/dashboard/myprofile']);
  }
  toStand(){
    this.resetMenu();
    this.optionMenu.stand=true;
    this._router.navigate(['/dashboard/mystand']);
  }
  toInbox(){
    this.resetMenu();
    this.optionMenu.inbox=true;
    this._router.navigate(['/dashboard/myinbox']);
  }
  getUser(){
    this.usId=parseInt(this._userService.getStorageUserId());
    if(this.usId){
      this._userService.getUser(this.usId).subscribe(
        success=>{
          console.log(success.data);
          this.profile=success.data.profile[0].fields;
          this.user=success.data.user[0].fields;
        },
        error=>{
          alert("Request error")
        }
      );
    }
  }
}



