import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { Category } from '../../../models/category';
import { CategoryService } from '../../../services/category/category.service';
import { Place } from '../../../models/place';
import { OauthService } from '../../../services/oauth/oauth.service';
import { PlaceService } from '../../../services/place/place.service';
import { NoticesService } from '../../../services/notices/notices.service';
import { Notice } from '../../../models/notice';

@Component({
  selector: 'app-new-stand-item-two',
  templateUrl: './new-stand-item-two.component.html',
  styleUrls: ['./new-stand-item-two.component.sass'],
  providers:[CategoryService,PlaceService,OauthService,NoticesService]
})
export class NewStandItemTwoComponent implements OnInit {
  private thing:string;
  private validData:any;
  private colors:any[];
  private initColor:any;
  private initColor2:any;
  private countColor:number;
  private hours:any[];
  private isProduct:boolean;//true=producto, false=service
  private notice:Notice;
  @ViewChild('status') status:ElementRef;
  @ViewChild('description') description:ElementRef;
  @ViewChild('time') time:ElementRef;
  constructor(
    private _route:ActivatedRoute,
    private _router:Router,
    private _categoryService:CategoryService,
    private _placeService:PlaceService,
    private _oauthService:OauthService,
    private _noticesService:NoticesService
  ) { 
    this.validData={
      color:true,
      time:true,
      status:true,
      description:true
    }
    this.colors=[];
    this.countColor=0;
    this.hours=[]; 
    this.notice=new Notice();
    this.notice.state="";
    this.notice.colors=[];
    this.notice.urgency=false;
    this.notice.time=0;
  }
  ngOnInit() {
    this.recoverType();
    this.createdTimeStructure();
  }
  recoverType(){
    this._route.params.forEach((params:Params)=>{
      this.thing=params["kind"];
      if(this.thing=="P"){
        this.isProduct=true;
      }else if(this.thing=="S"){
        this.isProduct=false;
      }
    });
  }
  createdTimeStructure(){
    for(var j=0;j<168;j++){
      this.hours.push({value:(j+1)});
    }
  }
  addColor(){
    this.countColor++;
    let color={
      id:this.countColor,
      value:"#f1f1f1"
    }
    this.colors.push(color);
  }
  deleteColor(idColor:number){
    for(var i=0;i<this.colors.length;i++){
      if(this.colors[i].id==idColor){
        this.colors.splice(i,1);
      }
    }
  }
  registerData(){
    if(this.validateData()){
      this.saveData();
      //this._router.navigate(['dashboard/updateresources',1]);
    }
  }
  validateData(){
    this.validData={
      color:true,
      time:true,
      status:true,
      description:true
    }
    let success=true;
    if(this.description.nativeElement.value==""){
      this.validData.description=false;
      success=false;
    }
    if(this.isProduct){
      if(this.colors.length==0){
        var $toastContent = $('<span>Debes crear al menos un color</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
        Materialize.toast($toastContent, 10000);
        success=false;
      }
      if(this.status.nativeElement.value=="Estado"){
        this.validData.status=false;
        success=false;
      }
    }else{
      if(this.time.nativeElement.value=="Numero de horas por semana"){
        this.validData.time=false;
        success=false;
      }
    }
    return success;
  }
  saveData(){
    let colorsSlect=[];
    if(typeof(Storage)){
      let urgency=this.notice.urgency;
      this.notice=JSON.parse(sessionStorage.getItem("notice"));
      if(this.notice){
        this.notice.description=this.description.nativeElement.value;
        this.notice.urgency=urgency;
        if(this.isProduct){
          this.notice.state=this.status.nativeElement.value;
          this.notice.colors=[];
          for(var i=0;i<this.colors.length;i++){
            this.notice.colors.push(this.colors[i].value);
          }
        }else{
          this.notice.time=this.time.nativeElement.value;
        }
      }
      debugger;
      this._noticesService.saveNotice(this.notice).subscribe(
        success=>{
          console.log(success);
          if(success.success){
            if(typeof(Storage)){
              sessionStorage.setItem("pkNotice",success.notice[0].pk);
              this._router.navigate(['dashboard/updateresources']);
            }else{
              var $toastContent = $('<span>Update your navigator</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
              Materialize.toast($toastContent, 10000);
            }
          }
        },
        error=>{
          var $toastContent = $('<span>Solicitud fallida, por favor vuelva a intentar</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 10000);
          console.log(error);
        }
      );
      console.log(this.notice);
    }
  }

}



