import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class InboxService {

  constructor(
    private _http:Http
  ) { }
  startConversation(data:any){
    return this._http.post('api/v1/apiServices/startConversation/',data,{}).map(result=>result.json());
  }
  getInbox(){
    return this._http.get('api/v1/apiServices/getInbox/',{}).map(result=>result.json());
  }
  sendMessage(data:any){
    return this._http.post('api/v1/apiServices/addMessage/',data,{}).map(result=>result.json());
  }
  getNotificationsNumber(){
    return this._http.get('api/v1/apiServices/getCountNotifications/',{}).map(result=>result.json());
  }
  readConversation(cvId:number){
    let data={
      conversation:cvId
    }
    return this._http.post('api/v1/apiServices/reviewConversation/',data,{}).map(result=>result.json());
  }
}



