import { Component, OnInit ,ViewChild, ElementRef } from '@angular/core';
import { OauthService } from '../../../services/oauth/oauth.service';
import { InboxService } from '../../../services/inbox/inbox.service';

@Component({
  selector: 'app-my-inbox',
  templateUrl: './my-inbox.component.html',
  styleUrls: ['./my-inbox.component.sass'],
  providers:[OauthService,InboxService]
})
export class MyInboxComponent implements OnInit {
  private conversations:any[];
  @ViewChild('message') message:ElementRef;
  private dataValid:any;
  private usId:number;
  constructor(
    private _oauthService:OauthService,
    private _inboxService:InboxService 
  ) {
    this.conversations=[];
    this.dataValid={
      message:true
    };
   }

  ngOnInit() {
    this.getInbox();
    if(typeof(Storage)){
      this.usId=parseInt(localStorage.getItem("usId"));
    }
  }
  getInbox(){
    this._inboxService.getInbox().subscribe(
      success=>{
        if(success.success){
          this.conversations=success.data;
          console.log(this.conversations);
        }
      },
      error=>{
        console.log(error);
      }
    );
  }
  validaMessage(){
    let success=true;
    if(this.message.nativeElement.value.length<1){
      this.dataValid.message=false;
      success=false;
    }
    return success;
  }
  sendMessage(input:string,ntId:number){
    debugger;
    if(input.length<1){
      this.dataValid.message=false;
    }else{
        let data={
          conversation:ntId,
          text:input
        }
        this._inboxService.sendMessage(data).subscribe(
          success=>{
            if(success.success){
              var $toastContent = $('<span>Mensaje enviado</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
              Materialize.toast($toastContent, 10000);
              this.getInbox();
            }
          },
          error=>{
            var $toastContent = $('<span>El mensaje no pudo ser enviado</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
            Materialize.toast($toastContent, 10000);
          }
        );
      }
  }
  openConversation(cvId:number){
    this._inboxService.readConversation(cvId).subscribe(
      success=>{
        console.log(success);
      },
      error=>{
        console.log(error);
      }
    );
  }
}



