import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { Category } from '../../../models/category';
import { CategoryService } from '../../../services/category/category.service';
import { Place } from '../../../models/place';
import { Notice } from '../../../models/notice';
import { OauthService } from '../../../services/oauth/oauth.service';
import { PlaceService } from '../../../services/place/place.service';

@Component({
  selector: 'app-new-stand-item',
  templateUrl: './new-stand-item.component.html',
  styleUrls: ['./new-stand-item.component.sass'],
  providers:[CategoryService,PlaceService,OauthService]
})
export class NewStandItemComponent implements OnInit {
  private typePublication:string;
  private categories:Category[];
  public places:Place[];
  public selectMultipleValues:any[];
  private validData:any;
  private notice:Notice;
  @ViewChild('typenotices') typenotices:ElementRef;
  @ViewChild('name') name:ElementRef;
  @ViewChild('category') category:ElementRef;
  @ViewChild('location') location:ElementRef;
  constructor(
    private _route:ActivatedRoute,
    private _router:Router,
    private _categoryService:CategoryService,
    private _placeService:PlaceService,
    private _oauthService:OauthService
  ) { 
    this.selectMultipleValues=[];
    this.validData={
      typeNotices:true,
      name:true,
      category:true,
      status:true,
      location:true,
      locationPrincipals:true,
      description:true
    }
    this.notice=new Notice();
    this.notice.kind=0;
    this.notice.thing=0;
    this.notice.title="";
    this.notice.category=0;
    this.notice.place=0;
    this.notice.locations=[];
  }

  ngOnInit() {
    this.recoverType();
    this.getCategories();
    this.getPlaces();
  }

  recoverType(){
    this._route.params.forEach((params:Params)=>{
      this.typePublication=params["type"];
      if(this.typePublication=="H"){
        this.notice.kind=1;
        this.typePublication="Lo que tengo"
      }else if(this.typePublication=="S"){
        this.typePublication="Lo que busco"
        this.notice.kind=2;
      }
      console.log(this.typePublication);
    });
  }
  getCategories(){
    this._categoryService.getCategories().subscribe(
      success=>{
        this.categories=success;
        console.log(this.categories);
      },
      error=>{
        alert("Request Error");
      }
    );
  }
  getPlaces(){
    this._placeService.getPlaces().subscribe(
      success=>{
        this.places=success;
        console.log(this.places);
      },
      error=>{
        alert("Request Error");
      }
    );
  }
  registerData(){
    if(this.validateData()){
      this.storageNotice();
      //this._router.navigate(['dashboard/updateresources',1]);
    }
  }
  validateData(){
    this.validData={
      typeNotices:true,
      name:true,
      category:true,
      location:true,
      locationPrincipals:true
    }
    let success=true;
    if(this.typenotices.nativeElement.value=="Seleccioné"){
      this.validData.typeNotices=false;
      success=false;
    }
    if(this.name.nativeElement.value==""){
      this.validData.name=false;
      success=false;
    }
    if(this.category.nativeElement.value=="Categoria"){
      this.validData.category=false;
      success=false;
    }
    if(this.location.nativeElement.value=="Ubicación"){
      this.validData.location=false;
      success=false;
    }
    if(this.selectMultipleValues.length==0 || this.selectMultipleValues.length>5){
      this.validData.locationPrincipals=false;
      success=false;
    }
    return success;
  }
  storageNotice(){
      this.notice.thing=this.typenotices.nativeElement.value;
      this.notice.title=this.name.nativeElement.value;
      this.notice.category=this.category.nativeElement.value;
      this.notice.place=this.location.nativeElement.value;
      this.notice.locations=this.selectMultipleValues;
      if(typeof(Storage)){
        sessionStorage.setItem('notice',JSON.stringify(this.notice));
        this.toStepTwo();
      }
  }
  toStepTwo(){
    this._router.navigate(['dashboard/newitemtwo',this.notice.thing]);
  }
}



