import {ModuleWithProviders} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {LandingPageComponent} from './components/landing-page/landing-page.component';
import { RegistrationStepOneComponent } from './components/user/registration/registration-step-one/registration-step-one.component';
import { RegistrationStepTwoComponent } from './components/user/registration/registration-step-two/registration-step-two.component';
import { RegistrationStepThreeComponent } from './components/user/registration/registration-step-three/registration-step-three.component';
//lobby
import { LobbyComponent } from './components/lobby/lobby.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
//dashboard
import { MyProfileComponent } from './components/dashboard/my-profile/my-profile.component';
import { MyPersonalDataComponent } from './components/dashboard/my-personal-data/my-personal-data.component';
import { MyStandComponent } from './components/dashboard/my-stand/my-stand.component';
import { MyInboxComponent } from './components/dashboard/my-inbox/my-inbox.component';
import { NewStandItemComponent } from './components/dashboard/new-stand-item/new-stand-item.component';
import { NewStandItemTwoComponent } from './components/dashboard/new-stand-item-two/new-stand-item-two.component';
import { ChangePasswordComponent } from './components/change-password/change-password.component';
import { UpdatesResourcesComponent } from './components/dashboard/updates-resources/updates-resources.component';
import { LogOutComponent } from './components/log-out/log-out.component';
import { ViewNoticesComponent } from './components/dashboard/view-notices/view-notices.component';
import { WallComponent } from './components/lobby/wall/wall.component';
import { OtherUserComponent } from './components/other-user/other-user.component';
import { ProfileOtherUserComponent } from './components/other-user/profile-other-user/profile-other-user.component';
import { StandOtherUserComponent } from './components/other-user/stand-other-user/stand-other-user.component';
import { ViewNoticesOtherUserComponent } from './components/other-user/view-notices-other-user/view-notices-other-user.component';
const appRoutes:Routes =[
    {path:'',component:LandingPageComponent},
    {path:'registeruserone/:email',component:RegistrationStepOneComponent},
    {path:'registerusertwo',component:RegistrationStepTwoComponent},
    {path:'registeruserthree',component:RegistrationStepThreeComponent},
    {
        path:'lobby',
        component:LobbyComponent,
        children:[
            {path:'wall',component:WallComponent}
        ]
    },
    {
        path:'otheruser',
        component:OtherUserComponent,
        children:[
            {path:'profileouser',component:ProfileOtherUserComponent},
            {path:'standouser',component:StandOtherUserComponent},
            {path:'viewnoticesouser/:idNotice',component:ViewNoticesOtherUserComponent}
        ]
    },
    {path:"logout",component:LogOutComponent},
    {path:"reset/:uid/:token",component:ChangePasswordComponent},
    {
        path:'dashboard',
        component:DashboardComponent,
        children:[
            {path:'myprofile',component:MyProfileComponent},
            {path:'mystand',component:MyStandComponent},
            {path:'mydatapersonal',component:MyPersonalDataComponent},
            {path:'myinbox',component:MyInboxComponent},
            {path:'newitem/:type',component:NewStandItemComponent},
            {path:'newitemtwo/:kind',component:NewStandItemTwoComponent},
            {path:'updateresources',component:UpdatesResourcesComponent},
            {path:'viewnotices/:idNotice',component:ViewNoticesComponent}
        ]
    },
    {path:'**',component:LandingPageComponent}
];
export const appRoutingProviders:any[]=[];
export const routing:ModuleWithProviders = RouterModule.forRoot(appRoutes);


