import { Component, OnInit,ViewChild, ElementRef } from '@angular/core';
import { NoticesService } from '../../../services/notices/notices.service';
import { InboxService } from '../../../services/inbox/inbox.service';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { UserInterfaceService } from '../../../services/user-interface/user-interface.service';
import { UserService } from '../../../services/user/user.service';

@Component({
  selector: 'app-wall',
  templateUrl: './wall.component.html',
  styleUrls: ['./wall.component.sass'],
  providers:[NoticesService,InboxService,UserService]
})
export class WallComponent implements OnInit {
  private usId:number;
  private itemsWall:any[];
  private itemsWallH:any[];
  private itemsWallS:any[];
  private ntId:number;
  @ViewChild('message') message:ElementRef;
  private validData:any;
  constructor(
    private _noticesService:NoticesService,
    private _inboxService:InboxService,
    private _route:ActivatedRoute,
    private _router:Router,
    private _userInterfaceService:UserInterfaceService,
    private _userService:UserService
  ) {
    this.itemsWallH=[];
    this.itemsWallS=[];
    this.validData={
      message:true
    }
    this.ntId=0;
  }
  ngOnInit() {
    this.getItemsWall();
    $('.modal').modal();
    $('.tooltipped').tooltip();
    this.listenSearcher();
  }
  getItemsWall(){
    if(typeof(Storage)){
      this.usId=parseInt(localStorage.getItem("usId"));
      this._noticesService.getWall(this.usId).subscribe(
        success=>{
          let data:any = success;
          data=JSON.parse(data._body);
          this.itemsWall=data.data;
          for(var i = 0;i<this.itemsWall.length;i++){
            //determinar los dias de publciacion
            var dateNow = new Date();
            var dateJoined= new Date(this.itemsWall[i].data.notice[0].fields.date);
            var timeDiff = Math.abs(dateNow.getTime() - dateJoined.getTime());
            var diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24)); 
            this.itemsWall[i].data.notice[0].fields.date=diffDays;

            //definir estado
            if(this.itemsWall[i].data.notice[0].thing[0].fields.state!=undefined){
              if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="N"){
                this.itemsWall[i].data.notice[0].thing[0].fields.state="Nuevo";
              }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="U"){
                this.itemsWall[i].data.notice[0].thing[0].fields.state="Usado";
              }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="E"){
                this.itemsWall[i].data.notice[0].thing[0].fields.state="Por encargo"
              }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="B"){
                this.itemsWall[i].data.notice[0].thing[0].fields.state="Restaurado";
              }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="R"){
                this.itemsWall[i].data.notice[0].thing[0].fields.state="Reparado";
              }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="M"){
                this.itemsWall[i].data.notice[0].thing[0].fields.state="Mejorado";
              }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="C"){
                this.itemsWall[i].data.notice[0].thing[0].fields.state="Cualquiera";
              }
            }
            // divir los items por tipo
            if(this.itemsWall[i].kind=="i_search"){
              this.itemsWallS.push(this.itemsWall[i]);
            }else{
              this.itemsWallH.push(this.itemsWall[i]);
            }

          }
          console.log(this.itemsWall);
          console.log(this.itemsWallS);
          console.log(this.itemsWallH);
        },
        error=>{
          console.log(error);
        }
      );
    }
  }
  modalComment(ntIdParam:number){
    this.ntId=ntIdParam;
    $('#modal-comment').modal("open");
  }
  startConversation(){
    this.validData.message=true;
    if(this.validateMessage()){
      let data={
        notice:this.ntId,
        text:this.message.nativeElement.value
      }
      this._inboxService.startConversation(data).subscribe(
        success=>{
          if(success.success){
            var $toastContent = $('<span>Mensaje enviado</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
            Materialize.toast($toastContent, 10000);
            $('#modal-comment').modal("close");
          }
          console.log(success);
        },
        error=>{
          var $toastContent = $('<span>El mensaje no pudo ser enviado</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 10000);
        }
      );
    }
  }
  validateMessage(){
    let success=true;
    if(this.message.nativeElement.value.length<1){
      this.validData.message=false;
      success=false;
    }
    return success; 
  }
  toProfile(oUsId:number){
    if(this._userService.setStorageOtherUserId(String(oUsId))){
      this._router.navigate(['otheruser/profileouser']);
    }
  }
  listenSearcher(){
    this._userInterfaceService.getSearcherData().subscribe(
      dataSearch=>{
        if(dataSearch!=undefined){
          debugger;
          console.log(dataSearch);
          this._noticesService.getSearch(dataSearch).subscribe(
            success=>{
              let data:any=success;
              if(data.status==200){
                let body = JSON.parse(data._body);
                if(body.data.length==0){
                  var $toastContent = $('<span>No hay resultados</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
                  Materialize.toast($toastContent, 10000);
                }else{
                  this.itemsWall=body.data;
                  for(var i = 0;i<this.itemsWall.length;i++){
                    //determinar los dias de publciacion
                    var dateNow = new Date();
                    var dateJoined= new Date(this.itemsWall[i].data.notice[0].fields.date);
                    var timeDiff = Math.abs(dateNow.getTime() - dateJoined.getTime());
                    var diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24)); 
                    this.itemsWall[i].data.notice[0].fields.date=diffDays;
        
                    //definir estado
                    if(this.itemsWall[i].data.notice[0].thing[0].fields.state!=undefined){
                      if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="N"){
                        this.itemsWall[i].data.notice[0].thing[0].fields.state="Nuevo";
                      }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="U"){
                        this.itemsWall[i].data.notice[0].thing[0].fields.state="Usado";
                      }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="E"){
                        this.itemsWall[i].data.notice[0].thing[0].fields.state="Por encargo"
                      }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="B"){
                        this.itemsWall[i].data.notice[0].thing[0].fields.state="Restaurado";
                      }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="R"){
                        this.itemsWall[i].data.notice[0].thing[0].fields.state="Reparado";
                      }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="M"){
                        this.itemsWall[i].data.notice[0].thing[0].fields.state="Mejorado";
                      }else if(this.itemsWall[i].data.notice[0].thing[0].fields.state=="C"){
                        this.itemsWall[i].data.notice[0].thing[0].fields.state="Cualquiera";
                      }
                    }
                  }
                  if(dataSearch.kind==1){
                    this.itemsWallH=this.itemsWall;
                  }else{
                    this.itemsWallS=this.itemsWall;
                  }

                }
              }
              console.log(success);
            },
            error=>{
              console.log(error);
            }
          );
        }
      }
    );
  }
}



