import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router, Params, ActivatedRoute } from '@angular/router';
import { NoticesService } from '../../../services/notices/notices.service';
import { Notice } from '../../../models/notice';
import { Category } from '../../../models/category';
import { CategoryService } from '../../../services/category/category.service';
import { PlaceService } from '../../../services/place/place.service';
import { Place } from '../../../models/place';
import { DropzoneDirective, DropzoneConfigInterface } from '../../../../../node_modules/ngx-dropzone-wrapper';
import { GLOBAL }  from '../../../services/enviroment/global';

@Component({
  selector: 'app-view-notices',
  templateUrl: './view-notices.component.html',
  styleUrls: ['./view-notices.component.sass'],
  providers:[NoticesService,CategoryService,PlaceService]
})
export class ViewNoticesComponent implements OnInit {
  private ntId:number;
  private dataPublication:any;
  private images:any[];
  private locations:any[];
  private thing:any;
  private videos:any;
  public categoriesFormated:string;
  public validData:any;
  private categories:Category[];
  private places:Place[];
  public selectMultipleValues:any[];
  public kind:number;
  public category:number;
  public location:number;
  public status:string;
  public colors:any[];
  private countColor:number;
  private hours:any[];
  private hourT:number;
  public configImg: DropzoneConfigInterface;
  @ViewChild('title') title:ElementRef;
  @ViewChild('description') description:ElementRef;
  @ViewChild(DropzoneDirective) directiveRef: DropzoneDirective;

  //selectMultipleOptions = [{value:'Option 1'}, {value:'Option 2'}, {value:'Option 3'}];
  //selectMultipleValues = this.selectMultipleOptions.slice(0, 2);

  constructor(
    private _route:ActivatedRoute,
    private _router:Router,
    private _noticesService:NoticesService,
    private _categoryService:CategoryService,
    private _placeService:PlaceService
  ) { 
    this.selectMultipleValues=[];
    this.dataPublication={};
    this.images=[];
    this.locations=[];
    this.thing={};
    this.videos={};
    this.categoriesFormated="";
    this.colors=[];
    this.hours=[]; 
    this.validData={
      type:true,
      title:true,
      category:true,
      location:true,
      publicOn:true,
      status:true,
      description:true,
      time:true
    }
    this.countColor=0;
  }
  ngOnInit() {
    $('.modal').modal();
    this._route.params.forEach((params:Params)=>{
      this.ntId=params["idNotice"];
      if(this.ntId!=null){
        this.getNotice(this.ntId);
        this.createdTimeStructure();
      }
    });
    this.initConfig();
  }
  getNotice(ntId){
    this._noticesService.getNotice(ntId).subscribe(
      success=>{
        console.log(success.data.notice[0]);
        this.dataPublication=success.data.notice[0].fields;
        this.images=success.data.notice[0].images;
        this.locations=success.data.notice[0].locations;
        this.thing=success.data.notice[0].thing[0].fields;
        this.videos=success.data.notice[0].videos;
        this.proccessData();
      },
      error=>{
        console.log(error);
      }
    );
  }
  createdTimeStructure(){
    for(var j=0;j<168;j++){
      this.hours.push({value:(j+1)});
    }
  }
  proccessData(){
    //definir tipò
    this.kind=this.dataPublication.kind;
    this.category=this.dataPublication.category;
    this.location=this.dataPublication.location;
    if(this.dataPublication.kind==1){
      this.dataPublication.kind='Lo que tengo';
    } else if(this.dataPublication.kind==2){
      this.dataPublication.kind='Lo que busco';
    }
    // consolidar done esta publicado
    for(var i=0;i<this.locations.length;i++){
      if(i!=0){
        this.categoriesFormated+=" - "+this.locations[i].location_name;
      }else if(i==0)
        this.categoriesFormated=this.locations[i].location_name;
    }
    this.status=this.thing.state;
    if(this.thing.state=='N'){
      this.thing.state="Nuevo";
    }else if(this.thing.state=='U'){
      this.thing.state="Usado";
    }else if(this.thing.state=='E'){
      this.thing.state="Por encargo";
    }else if(this.thing.state=='B'){
      this.thing.state="Restaurado";
    }else if(this.thing.state=='R'){
      this.thing.state="Reparado";
    }else if(this.thing.state=='M'){
      this.thing.state="Mejorado";
    }else if(this.thing.state=='C'){
      this.thing.state="Cualquiera";
    }
    /*for(var i=0;i<this.locations.length;i++){
      this.selectMultipleValues.push(this.locations[i].location);
    }
    console.log(this.selectMultipleValues);*/
    this.getCategories();
    this.getPlaces();
    this.colors=[];
    if(this.thing.colors!=undefined){
      for(var k=0;k<this.thing.colors.length;k++){
        this.colors.push({value:k,color:this.thing.colors[k].color});
      }
      this.countColor=this.colors.length;
    }else{
      this.hourT=this.thing.time;
    }
  } 
  getCategories(){
    this._categoryService.getCategories().subscribe(
      success=>{
        this.categories=success;
      },
      error=>{
        alert("Request Error");
      }
    );
  }
  getPlaces(){
    this._placeService.getPlaces().subscribe(
      success=>{
        this.places=success;
        this.processPlaces();
      },
      error=>{
        alert("Request Error");
      }
    );
  }
  processPlaces(){
    for(var i=0;i<this.locations.length;i++){
      for(var j=0;j<this.places.length;j++){
        if(this.locations[i].location==this.places[j].id){
          this.selectMultipleValues.push(this.places[j]);
        }
      }
    }
  }
  viewList(){
    console.log(this.selectMultipleValues);
  }
  modalType(){
    $('#modal-type').modal("open");
  }
  modalTitle(){
    $('#modal-title').modal("open");
  }
  modalCategory(){
    $('#modal-category').modal("open");
  }
  modalLocation(){
    $('#modal-location').modal("open");
  }
  modalPublicOn(){
    $('#modal-public-on').modal("open");
  }
  modalStatus(){
    $('#modal-status').modal("open");
  }
  modalDescription(){
    $('#modal-description').modal("open");
  }
  modalTime(){
    $('#modal-time').modal("open");
  }

 
  

  //actualiza titulo
  updateTitle(){
    if(this.validateTitle){
      let dataTitle={
        notice:this.ntId,
        title:this.title.nativeElement.value
      }
      this.updateData(dataTitle);
    }
  }
  //validar titulo
  validateTitle(){
    let success=true;
    if(this.title.nativeElement.value.length<1 || this.title.nativeElement.value.length>40){
      this.validData.title=false;
      success=false;
    }
    return success;
  }
  //actualiza categoria
  updateCategory(){
      let dataCategory={
        notice:this.ntId,
        category:this.category
      }
      this.updateData(dataCategory);
  }
  //actualiza place
  updateLocation(){
    let dataPlace={
      notice:this.ntId,
      place:this.location
    }
    this.updateData(dataPlace);
  }
  //actualiza locations
  updateLocations(){
    let newLocations=[];
    for(var i=0;i<this.selectMultipleValues.length;i++){
      newLocations.push(String(this.selectMultipleValues[i].id));
    }
    let dataLocation={
      notice:this.ntId,
      locations:newLocations
    }
    this.updateData(dataLocation);
  }
  updateStatus(){
    let dataStatus={
      notice:this.ntId,
      state:this.status
    }
    this.updateData(dataStatus);
  }
  //actualiza description
  updateTime(){
      let dataTime={
        notice:this.ntId,
        time:this.hourT
      }
      this.updateData(dataTime);
  }
  //actualiza description
  updateDescription(){
    if(this.validateDescription){
      let dataDescription={
        notice:this.ntId,
        description:this.description.nativeElement.value
      }
      this.updateData(dataDescription);
    }
  }
  //validar description
  validateDescription(){
    let success=true;
    if(this.description.nativeElement.value.length<1 || this.description.nativeElement.value.length>1000){
      this.validData.description=false;
      success=false;
    }
    return success;
  }
  //actualiza description
  updateColors(){
    let colorsUpdated=[];
    if(this.validateColors()){
      for(var i=0;i<this.colors.length;i++){
        colorsUpdated.push(this.colors[i].color);
      }
      let dataColors={
        notice:this.ntId,
        colors:colorsUpdated
      }

      this.updateData(dataColors);
    }
  }
  // validar los colores
  validateColors(){
    let success=true;
    if(this.colors.length==0){
      success=false;
    }
    return success;
  }
  // agreagr color
  addColor(){
    this.countColor++;
    let color={
      value:this.countColor,
      color:"#f1f1f1"
    }
    this.colors.push(color);
  }
  //actualiza los datos
  updateData(data:any){
    this._noticesService.updateNotices(data).subscribe(
      success=>{
        this.getNotice(this.ntId);
        var $toastContent = $('<span>Actualizacion exitosa</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
        Materialize.toast($toastContent, 5000);
      },
      error=>{
        console.log(error);
        if(error.status==422){
          var $toastContent = $('<span>El dato ya está registrado</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 5000);
        }else{
          var $toastContent = $('<span>Error en la petición</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
          Materialize.toast($toastContent, 5000);
        }
      }
    );
  }
  //elimina el color
  deleteColor(valueColor){
    for(var i=0;i<this.colors.length;i++){
      if(this.colors[i].value==valueColor){
        this.colors.splice(i,1);
      }
    }
  }

  onUploadError(any:any){
    console.log(any);
    if(any.status==401){
      var $toastContent = $('<span>Error al subir imagen</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
      Materialize.toast($toastContent, 10000);
    }
  }
  onUploadSuccess(any){
    var $toastContent = $('<span>Imagen almacenada exitosamente</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
    Materialize.toast($toastContent, 10000);
    console.log(any);
    this.images=[];
    this.getNotice(this.ntId);
    //this.iconUpload=false;
    this.resetDropzone();
  }
  resetDropzone(){
    this.directiveRef.reset();
  }
  initConfig(){
    let key;
    if(typeof(Storage) !== "undefined") {
      key=localStorage.getItem("key");
      this.configImg= {
        url: GLOBAL.apiGeepy+"api/v1/images/",
        paramName: "image",
        thumbnailWidth: 320,
        thumbnailHeight: 200,
        resizeHeight: 1000,
        resizeWidth: 330,
        method:"POST",
        acceptedFiles: "image/jpeg,image/png,image/jpg",
        headers: {
          'Cache-Control': null,
          'X-Requested-With': null,
          'Authorization': 'Token '+key
        },
      };
    }
  }
  sending(anyP:any){
    anyP[2].append("notice",String(this.ntId));
   //anyP[2].notice=String(this.ntId);
    console.log(anyP[2]);
  }
  // eliminar imagen
  deleteImage(imgId:number,event){
    this._noticesService.deleteImage(imgId).subscribe(
      success=>{
        this.images=[];
        this.getNotice(this.ntId);
        var $toastContent = $('<span>Imagen eliminada exitosamente</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
        Materialize.toast($toastContent, 10000);
      },
      error=>{
        var $toastContent = $('<span>la imagen no se pudo eliminar</span>').add($('<button class="btn-flat toast-action">Undo</button>'));
        Materialize.toast($toastContent, 10000);
      }
    );
  }
}



