import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class CategoryService {

  constructor(
    private _http: Http
  ) { }
   getCategories(){
    return this._http.get('api/v1/categories',{}).map(res=>res.json());
  }
}



